* {
  box-sizing: border-box;
  background-color: #272829;
  direction: rtl;
}



.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
}

.box {
  font-size: 24px;
  color: #FFF6E0;
  padding: 10px;
  font-weight: bolder;
  outline: none;
  border-radius: 13px;
}

.top {
  gap: 10px;
  display: flex;
}

.flex {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.buttom {
  font-size: 1.5rem;
  width: 100px;
}

.buttom:hover {
  color: #FFF6E0;
  background-color: #61677A;

}

.styled-table {
  border-collapse: collapse;
  /* margin: 25px 0; */
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  color: #FFF6E0;
  font-size: 1.5rem;

}

.styled-table th {
  text-align: center;
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: right;
}

.styled-table th,
.styled-table td {
  padding: 12px 15px;
}

.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}

.styled-table tbody tr.active-row {
  font-weight: bold;
  color: #009879;
}

.flex2 {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  /* margin-top: ; */
}

svg {
  color: #009879;
}